footer {
    display: flex;
    flex-direction: column-reverse;
    gap: 2rem;

    .brand-container {
        text-align: center;
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .brand {
            display: flex;
            justify-content: center;
            gap: 0.3rem;

            span {
                font-size: 2rem;
                color: var(--text-color-alt);
                font-weight: bolder;
                font-family: Verdana, Geneva, Tahoma, sans-serif;
            }

            .title {
                color: var(--red);
            }
        }

        .description {
            color: var(--text-color-alt);
        }

        .social-links {
            display: flex;
            gap: 2rem;
            justify-content: center;

            li {
                background-color: var(--red);
                padding: 0.8rem;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 3rem;
                transition: var(--transition);

                &:hover {
                    background-color: white;

                    svg {
                        color: var(--red);
                    }
                }

                svg {
                    color: white;
                    font-size: 1.2rem;
                }

                a {
                    font-size: 0;
                }
            }
        }

        .copy-right {
            color: var(--red);
            line-height: 2;

            span {
                color: var(--text-color-alt);
                font-size: 18px;
                font-weight: 700;
            }

            a {
                text-decoration: none;
                font-size: 18px;
                font-weight: 700;
                color: var(--text-color-alt);
            }
        }
    }

    .links {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;

        .link {
            display: flex;
            flex-direction: column;
            gap: 2rem;

            .title {
                color: var(--text-color-alt);
            }

            ul {
                display: flex;
                flex-direction: column;
                gap: 1rem;

                li {
                    a {
                        text-decoration: none;
                        color: var(--text-color);
                    }
                }
            }
        }
    }

    @media only screen and (min-device-width: 768px) {
        flex-direction: row;
        gap: 5rem;

        .brand-container {
            width: 40%;
        }
    }

    @media screen and (min-device-width: 1200px) {
        .links {
            grid-template-columns: repeat(4, 1fr);
        }
    }
}
